<template>
  <playerControler  @isplaying='isplaying'></playerControler>
  <router-view/>
</template>

<script>
import playerControler from './components/playerControler.vue';
import {provide,reactive} from 'vue';
export default {
  components:{
    playerControler,
  },
  setup() {

    const musicplay=reactive({
      List:[],
      index:0,
      id:0,
      uname:'',
      imgurl:'',
      isplaying:false,
      curtime:0
    })
    const updata= (key, val)=> {
      musicplay[key] = val
    }

    provide('musicplay',{
      val:musicplay,
      updata
    })

    return{
      musicplay
    }
  },

  methods: {
    isplaying(val){
      // console.log(val)
      this.musicplay.isplaying=val
    }
  },

}
</script>


